import React from 'react';

import CardEditMenu from './CardEditMenu'

const TaskTableCell = (props) => {
  
    const {id, task, assign_to, priority, due_by_date} = props.data;
    //console.log("ass", assign_to);
    const {onSelectTask} = props;
    const options = ([
        {content: 'Modify', id: 1},
        {content: 'Delete', id: 2}
    ]);

    return (
        <tr
            tabIndex={-1}
            key={id}
        >
            <td>{task}</td>
            <td>{assign_to.join(', ')}</td>
            <td>{priority > 0 ? 'High':'Low'}</td>
            <td>{due_by_date}</td>
            <td className="text-right">
                <CardEditMenu key={id} selectedData={id} onSelectRow={onSelectTask} menu_rows={options}/>
            </td>
        </tr>
    );
};

export default TaskTableCell;
