import React from 'react';

import CardEditMenu from './CardEditMenu'

const ProjectTableCell = (props) => {
  
    const {id, project_name, manager_name, user_name, survey_start_date, status} = props.data;
    const {onSelectProject} = props;
    const options = ([
        {content: 'Modify', id: 1},
        {content: 'Delete', id: 2}
    ]);

    return (
        <tr
            tabIndex={-1}
            key={id}
        >
            <td>{project_name}</td>
            <td>{manager_name}</td>
            <td>{user_name}</td>
            <td>{survey_start_date}</td>
            <td>{status > 0 ? 'Yes':'No'}</td>
            <td className="text-right">
                <CardEditMenu key={id} selectedData={id} onSelectRow={onSelectProject} menu_rows={options}/>
            </td>
        </tr>
    );
};

export default ProjectTableCell;
