import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import {public_url} from "config";

import CardEditMenu from './CardEditMenu'

const UserTableCell = (props) => {

    const {id, first_name, last_name, job_title, email, mobile, user_type, status, profile_pic} = props.data;
    const {onSelectUser} = props;
    const options = ([
        {content: 'Modify', id: 1},
        {content: 'Delete', id: 2}
    ]);

    return (
        <tr
            tabIndex={-1}
            key={id}
        >
            <td>
                <div className="user-profile d-flex flex-row align-items-center">
                    <Avatar
                        alt={first_name}
                        src={public_url + "upload/img/" + profile_pic}
                        className="user-avatar"
                    />
                    <div className="user-detail">
                        <h5 className="user-name">{first_name} {last_name}</h5>
                    </div>
                </div>
            </td>
            <td>{job_title}</td>
            <td>{email}</td>
            <td>{mobile}</td>
            <td>{user_type == '6' ? 'Normal' : user_type == '2' ? 'Admin' : 'NONE'}</td>
            <td>{status ? 'Yes':'No'}</td>
            <td className="text-right">
                <CardEditMenu key={id} selectedData={id} onSelectRow={onSelectUser} menu_rows={options}/>
            </td>
        </tr>
    );
};

export default UserTableCell;
