import React from 'react';
import CardEditMenu from './CardEditMenu'

const RoomTableCell = (props) => {

    const {id, room_number, site_name, ceiling_height, ceiling, wall} = props.data;
    const {onSelectRoom} = props;
    const options = ([
        {content: 'Modify', id: 1},
        {content: 'Delete', id: 2}
    ]);

    return (
        <tr
            tabIndex={-1}
            key={id}
        >
            <td>{room_number}</td>
            <td>{site_name}</td>
            <td>{ceiling_height}</td>
            <td>{ceiling}</td>
            <td>{wall}</td>
            <td className="text-right">
                <CardEditMenu key={id} selectedData={id} onSelectRow={onSelectRoom} menu_rows={options}/>
            </td>
        </tr>
    );
};

export default RoomTableCell;
