import React from 'react';

import CardEditMenu from './CardEditMenu'

const SiteTableCell = (props) => {

    const {id, site_name, city, address, postcode, rooms, projects} = props.data;
    const {onSelectSite} = props;
    const options = ([
        {content: 'Modify', id: 1},
        {content: 'Delete', id: 2}
    ]);

    return (
        <tr
            tabIndex={-1}
            key={id}
        >
            <td>{site_name}</td>
            <td>{city}</td>
            <td>{address}</td>
            <td>{postcode}</td>
            <td>{rooms}</td>
            <td>{projects}</td>
            <td className="text-right">
                <CardEditMenu key={id} selectedData={id} onSelectRow={onSelectSite} menu_rows={options}/>
            </td>
        </tr>
    );
};

export default SiteTableCell;
